<template>
      <!-- O main sempre vai ser 100% do viewheitgh - (cabealho+rodap)-->
  <main>
  <div>

    <b-card>
      <h4 class="card-title text-center titulo-paginas"> Cadastro de Operaes </h4>
      <b-tabs card>
        <!-- Render Tabs, supply a unique `key` to each tab -->
        <b-tab v-for="i in tabs" :key="'dyn-tab-' + i" :title="'Operao ' + (i+1)">
          <!-- ID = {{ i }} -->
          <div class="btnCancel">
            <b-button size="sm" variant="danger" class="float-right" @click="closeTab(i)">
                  <i class="fas fa-times"></i>
                  Cancelar Operao
            </b-button>
          </div>
          <!-- COMPONENTE DE FORMULRIO DE CADASTRO OPERAES -->
                            <FormOperacoes />
          <!-- COMPONENTE DE FORMULRIO DE CADASTRO OPERAES -->
        </b-tab>

        <!-- New Tab Button (Using tabs-end slot) -->
        <template #tabs-end>
          <b-nav-item role="presentation" @click.prevent="newTab" href="#">
            <b>+</b> Nova Operao
          </b-nav-item>
        </template>

        <!-- Render this if no tabs -->
        <template #empty>
          <div class="text-center text-muted">
            Sem operaes abertas.<br>
            Abra uma nova aba de operaes com o boto acima.
          </div>
        </template>
      </b-tabs>
    </b-card>
  </div>
  </main>
</template>

<script>
  import FormOperacoes  from '@/pages/FormOperacoes';

  export default {
    components: {
        FormOperacoes,
    },
    name:'AddOperacoes',
    data() {
      return {
        tabs: [],
        tabCounter: 0
      }
    },
    created()   
    {
      // this.authenticationControl();
      this.$store.commit('setIsPageEditable', true );
      this.newTab();
    },
    methods: 
    { 
      closeTab( x )   {
          for (let i = 0; i < this.tabs.length; i++)  {
            if ( this.tabs[i] === x )  
                this.tabs.splice(i, 1)
          }
          this.tabCounter--;
      },
      newTab()        {
          this.tabs.push(this.tabCounter++)
      }
    }
  }
</script>

<style>
.btnCancel      {
 content: "";
 padding-left: 85%;
}

.btn-container  {
 display: flex;
 justify-content: center;
}
</style>